@import url('https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap');

#Main{
  max-width: 1311px;
}


.backgroundImage {
  background: url("Styling/background.jpg");
  height: 100vh;
  width: 100vw;
  
  background-size: cover;  
  align-items: center;
  justify-content: center;

}

.ebook-backgroundImage{
  background: url("Styling/decorative-border.png");
  /* height: 100vh;
  width: 100vw;   */
  /* background-size: cover;   */
  align-items: center;
  justify-content: center;
  margin: auto;
  background-repeat: no-repeat;
  /* background-color: rgb(81, 12, 90); */
  background-position-x: center;
  
}

.menuStyling{
  padding-right: 40px;
  float: right;
  text-align: right;
  font-family: "Indie Flower";
}

.wrapStyling{
  display: flex;
  font-family: "Indie Flower";
  position: absolute;
  top: 50%;
  left: 30%;

}

.parallax{
  min-height: 650px;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  
}

#Contact.parallax{
  min-height: 550px;
  
}

img{
  float: left;
}

article{
  float: right;
}

.Content{
  
  font-family: "Indie Flower";
  padding-top: 5%;
}

.content-heading1{
  text-align: center;
  display: flex;  
  padding-left: 45%;
  
}

.ebook-heading2{
  text-align: center;
  display: flex;  
  padding-left: 40%;
  padding-top: 100px;
  
}

.w5 {
  width: 14rem;
}

#Contact{
  
  font-family: "Indie Flower";
  padding-top: 5%;
  background: rgb(47, 9, 68);
  color: #fff;
}

.contact-heading1{
  text-align: center;
  display: flex;  
  padding-left: 45%;
  color: white;
  
}

#comment.measure {
  min-height: 5em;
  min-width: 8em;
}

.measure2 {
  max-width: 60rem;
}

.thankyou_message{
  display:none;
}

.gform{
  display: block;
  float: left;
}



footer{
  font-family: "Indie Flower";
  padding-top: 5%;
  background: rgb(47, 9, 68);
  color: #fff;
}

a {
  text-decoration: none;
  color: rgb(47, 9, 68);
}

a:hover{
  text-decoration: none;
  color: rgb(81, 12, 90);
}

.ebook-contents{
